@for $j from 1 through 31 {

  .calendar div:nth-child(#{$j}) {
    background-color: #eee;
    display: flex;
    justify-content: center;
    width: $cell;
    height: $cell;
    border: 1px solid black;
    transition: transform 0.5s, background-color 0.5s linear;

    &:hover {
      transform: translate(0, -20px);
      background-color: #ffbfcb;
    }

    &::after {
      content: "#{$j}";
      align-self: center;
    }
  }
}

@for $i from 28 through 30 {
  .calendar-#{$i} {
    div:nth-child(n + #{$i + 1}) {
      display: none;
    }
  }
}

@each $weekday, $gapday in  $weekdays {
  .calendar-#{$weekday} div:first-child {
    margin-left: #{$gapday};
  }
}
